@media (max-width: 767.98px) {
  .home .scrollbar-container.cs-sidebar.cs-sidebar--left {
    flex-basis: 100% !important;
    max-width: 100%;
  }

  .home .cs-main-container .cs-chat-container {
    flex-basis: 0% !important;
    display: none;
  }

  .chat .scrollbar-container.cs-sidebar.cs-sidebar--left {
    flex-basis: 0% !important;
    display: none;
  }

  .chat .cs-main-container .cs-chat-container {
    flex-basis: 100% !important;
  }
}

.cs-message--incoming .cs-message__content {
  background-color: #e5e6eb !important;
}

.cs-message--outgoing .cs-message__content {
  background-color: #0099ff !important;
  color: white !important;
}

section.pending > div.cs-message__content-wrapper > div.cs-message__content {
  background-color: #6ea9d7 !important;
}