.fade-out:after {
    content: '';
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-image: linear-gradient( rgba(255, 255, 255, 0) 50%, rgba(255, 255, 255, 1) 100% );
    pointer-events: none;
}

.approved .fade-out:after {
    content: '';
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-image: linear-gradient( rgba(207, 216, 220, 0) 50%, rgba(207, 216, 220, 1) 100% );
}

.MuiButton-outlinedPrimary {
    margin-top: 0.8em !important;
    margin-right: 0.5em !important;
}

.doc-plan {
    padding: 6px 6px;
    color: #455a64;
    font-weight: bold;
    border: 2px solid #455a64;
}

.restore-btn {
    margin-top: 0 !important;
}